	export default {
		data() {
			return {
				list:[],
				name:"",
				phone:"",
				area:""
			}
		},
		onLoad:function(){ 
			var _self = this
			_self.getInfo()
		},
		methods: {
			getInfo:function(){
				var _self = this
				uni.request({
					url:_self.$api+"dockingManager/releaseMainQuery",
					data:{id:"0",pull:1,optionId:uni.getStorageSync("openId")},
					method:"GET", 
					success:function(res){
						console.log(res)
						var data = res.data
						_self.list = res.data
					}
					
				})
			},
			enterApply:function(){
				var _self = this
				var myreg = /^((0\d{2,3}-\d{7,8})|(1[34578]\d{9}))$/;
				if(_self.name == ""){
						uni.showToast({
						title:"请输入联系人",
						icon:"none"
					})
					return false
				}else if(!myreg.test(_self.phone)){
						uni.showToast({
						title:"联系电话格式不正确",
						icon:"none"
					})
					return false
				}else if(_self.area==""){
						uni.showToast({
						title:"请输入需要面积",
						icon:"none"
					})
					return false
				}
				uni.request({
					url:_self.$api+"dockingManager/admissionAdd",
					data:{
						name:_self.name,phone:_self.phone,noddles:_self.area,optionId:uni.getStorageSync("openId")
					},
					success:function(res){
						console.log(res)
						if(res.data ==1){
							uni.showToast({
								title:"申请成功",
								success:function(){
									setTimeout(function(){
										uni.switchTab({
											url:"../../../pages/index/index"
										})
									},1000)
								}
							})
							return false
						}
						if(res.data ==99){
								uni.showModal({
									title: '提示',
									content: '此操作需用户授权，是否进行授权',
									success: function (res) {
										if (res.confirm) {
											//跳转到授权页面  
											uni.navigateTo({
												url:"../../login/login"  
											})
											console.log('用户点击确定');
										} else if (res.cancel) {
											console.log('用户点击取消');
										}
									}
								});
								return false
						}
						else{
								uni.showToast({
								title:"申请失败",
								icon:"none"
							})
							return false
						}
					},
				})
			},
			
		},
		 filters:{
			 imgData:function(newdata){
				 return newdata.replace(/<img/gi, '<img style="max-width:100%;height:auto;display:block"')
                                        .replace(/<section/g,'<div')
                                        .replace(/\/section>/g,'\div>');
			 }
		 }
		 
	}
